import React from 'react'
import axios from 'axios';
import { useParams } from 'react-router-dom'
import { useState, useEffect } from 'react';
import { mnt } from './data';


function Detail() {
  let param = useParams();
  const [data, setData] = useState();
  
  console.log(param.name, param.id);
  //20000001
  
  async function loadData() {
    let msg = param.name
    
    // const load = await axios.get('http://localhost:3030',{params:{msg}})
    
    const load = await axios.get('http://localhost:3000',{params:{msg}})
    let newData = load.data;

    if(newData.length){
      newData = newData.filter(obj => {
        return obj.mntnid == param.id;
      })[0];
    }
   
    setData(newData)
  }
  
  useEffect(() => {
    loadData();
  }, [])


  if(!data) return <>loading....</>;
  return (
    <article>
  {/* <p>{data.mntninfodscrt}</p>
      <div>
        <img src={`${data.hndfmsmtnmapimageseq}`} alt='' />
      </div>

      <div>
        <img src={`${data.mntnattchimageseq}`} alt='' />
      </div> */}

        <ul>
        {/* {
          data.map((v,k)=>(
            
            <li key={k}>
              <p>{v.mntnnm}</p>
              <p>{v.mntninfopoflc}</p>
              <p>{v.mntninfohght}m</p>
            </li>
            
          ))
        } */}
        <li>
              <p>{data.mntnnm}</p>
              <p>{data.mntninfopoflc}</p>
              <p>{data.mntninfohght}m</p>
        </li>
        </ul>

    </article>
  )
}

export default Detail